/* Utilities */
body {
  margin: 0;
  font-family: "Shadows Into Light", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
  font-size: 20px;
  box-sizing: border-box !important;
}

div {
  color: white;
  max-width: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2%;
}

img {
  width: 250px;
  height: 300px;
  padding: 5px;
}

.hidden {
  display: none;
}

.container {
  border-radius: 5%;
  border-color: white;
  height: 90vh;
  position: relative;
  text-align: center;
}

.next {
  position: absolute;
  bottom: 5%;
  left: 35%;
  margin: 0 auto;
  padding: 10px;
  width: 200px;
}

.btn-holder {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  height: 50%;
}

.btn-holder > button {
  width: 50px;
  height: 50px;
  margin: 5px;
}

.btn-gradient {
  background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);
}
.selected {
  background-image: linear-gradient(
    to right,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
}

.btn-dracula {
  margin-right: 5px;
  color: white;
  background-image: linear-gradient(
    to right,
    #b8cbb8 0%,
    #b8cbb8 0%,
    #b465da 0%,
    #cf6cc9 33%,
    #ee609c 66%,
    #ee609c 100%
  );
}

/* Main Page */
.headline {
  margin: 10px;
  color: red;
}

.count {
  width: 175px;
  font-size: 17px;
  margin-bottom: 5px;
}

.count:disabled {
  opacity: 1 !important;
}

.classes {
  min-width: 400px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.class {
  min-width: 100px;
}
.names {
  margin: 0;
  min-width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.playernumber {
  height: 40px;
  width: 35px;
}
.playernumber:disabled {
  opacity: 1 !important;
}

.name-input {
  min-width: 75%;
}
.name {
  height: 40px !important;
}

/* Waiting Page */
.skip {
  text-align: center;
  height: 90vh;
}

.skip > button {
  margin-top: 90% !important;
}

@media only screen and (max-width: 1000px) {
  body {
    font-size: 11px;
  }

  h2 {
    font-size: 24px;
  }

  h4 {
    margin-top: 6px;
  }

  p {
    font-size: 15px;
  }

  div {
    color: white;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10%;
  }

  img {
    width: 200px;
    height: 250px;
    padding: 5px;
  }

  .btn-holder {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    height: 50%;
  }

  .container {
    width: 100vw !important;
    margin-left: 0;
  }

  /* Main Page */
  .headline {
    margin: 10px;
    color: red;
  }

  .classes {
    margin-top: 5px;
    min-width: 200px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .class {
    min-width: 100px;
    font-size: 15px;
  }
  .names {
    margin: 0;
    max-width: 90%;
    display: grid;
    grid-auto-rows: 60px;
    grid-template-columns: repeat(2, 1fr);
  }

  .playernumber {
    height: 35px;
  }

  .name {
    height: 35px !important;
  }

  .next {
    position: absolute;
    bottom: 5%;
    left: 25%;
    margin: 0 auto;
    padding: 10px;
    width: 200px;
  }

  .btn-holder {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    height: 50%;
  }

  .btn-holder > button {
    font-size: 11px;
    width: 70px;
    height: 30px;
    margin: 3px;
  }

  /* Waiting Page */
  .skip > button {
    margin-top: 110% !important;
  }

  /* Vote Page */
  .vote-buttons {
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  .vote-btn {
    width: 100px;
    height: 75px;
  }

  .select {
    width: 100px;
  }
}
